

.chuck-page {
  
}

.chuck-input {
  padding: 5px;
}

.chuck-input input {
  width: 99.3%;
  height: 36px;
  font-size: 28px;
}

.chuck-input button {
  width: 50%;
  height: 32px;
}

.chuck-result {
  min-height: 150px;
  border: 1px solid #CCC;
  border-radius: 5px 5px 0 0 ;
  margin: 5px; 
  padding: 5px; 
  background-color: beige;
}

.chuck-wisdom {
  text-align: left;
}

.chuck-wisdom .single-fact {
  margin: 5px;
  margin-bottom: 10px;
  border: 1px solid #AAA;
  border-radius: 10px;
  background-color: #DDD;
  padding: 10px;
  font-size: larger;
}

.instructions {
  font-size: smaller;
  text-align: center;
}

.chuck-categories .loading,
.chuck-result .loading {
  text-align: center;
}

.chuck-categories {
  text-align: center;
  width: 100%;
  float: left;
}

.chuck-category-wrapper {
  position: relative;
  float: left;
  width: 100px; 
  height: 100px;
  /*background-color: cyan;*/
  margin: 5px;
  border: 1px solid #666;
  border-radius: 5px;
  text-align: center;
}

