.chuck-category {
  position: relative;
  float: left;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-color: #EEE;
  width: 100%;
  opacity: 0.7;
  font-size: larger;
  font-weight: bold;
}

.chuck-footer {
  clear: both;
  text-align: center;
}

.chuck-footer small {
  font-size: 6pt;
  font-weight: normal;
}



